import { NgModule } from '@angular/core';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { TestComponent } from './test/test.component';
import { MenuComponent } from './menu/menu.component';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { far } from '@fortawesome/free-regular-svg-icons';
import { AppHeaderComponent } from './dashboard-header/dashboard-header.component';
import { AppPostComponent } from './post-block/post-block.component';

@NgModule({
    imports: [
        CommonModule,
        BrowserModule,
        ReactiveFormsModule,
        FormsModule,
        HttpClientModule,
        FontAwesomeModule
    ],
    declarations: [TestComponent, MenuComponent, AppHeaderComponent, AppPostComponent],
    exports: [TestComponent, MenuComponent, AppHeaderComponent, AppPostComponent]
})
export class SharedModule {
    constructor() {
        library.add(fas, far);
    }
}
